<template>
	<view class="video">
		<view class="cu-card dynamic no-card solid-top" v-if="!!list && list.length > 0">
			<view class="cu-item shadow" v-for="(item,index) in list" :key="index">
				<view class="cu-list menu-avatar">
					<view class="cu-item">
						<view class="cu-avatar round lg" :style="'background-image:url(' + item.user_id_info.avatar + ');'"></view>
						<view class="content flex-sub">
							<view>{{ item.user_id_info.name }}</view>
							<view class="text-gray text-sm flex justify-between">
								{{ item.created_at }}
							</view>
						</view>
					</view>
				</view>
				<view class="text-content">
					{{ item.text_body }}
				</view>
				<view class="grid flex-sub padding-lr col-1">
					 <video-box :imgList="[item.video_url]" :num="1"></video-box>
				</view>
				<view class="flex align-center justify-end padding-lr padding-bottom padding-top-sm">
					<view class="flex align-center text-lg text-gray" @tap.stop.prevent="videoLike(item,index)">
						<text class="margin-right-xs" :class="item.is_like?'cuIcon-appreciatefill text-kartyellow':'cuIcon-appreciate'"></text> 
						{{ item.like_count }}
					</view>
					<!-- <text class="cuIcon-comment margin-left margin-right-xs"></text> 
					{{ item.feed_comment_count }} -->
				</view>
			</view>
			<uni-load-more :status="status"></uni-load-more>
		</view>
		<view class="emptyContainer tet-lg text-black" v-else>
			暂无更多数据
		</view>
			
		<!--  -->
	</view>
</template>

<script>
var that
import { getVideoList, videoLike } from '@/utils/api.js'
export default {
	data() {
		return {
			test: '../../static/image/banner2.png',
			list: [],
			status: 'more',
			page: 1,
			lastPage: 0,
		}
	},
	onLoad(option) {
		that = this
		that.getVideoList(1)
	},
	// 下拉刷新
	onPullDownRefresh() {
		that.page = 1
		that.getVideoList(1)
	},
	//上拉加载
	onReachBottom(){
		console.log(that.page);
		console.log(that.lastPage);
		if (that.lastPage >= that.page) {
			that.getVideoList(that.page)
			return
		}
		that.status = 'noMore'
	},
	methods: {
		getVideoList(page){
			getVideoList(page).then(res => {
				console.log('getVideoList',res)
				if(res.statusCode == 200){
					let art = res.data
					that.lastPage = art.meta.last_page
					if (page == 1) {
						that.list = []
					}
					that.list = that.list.concat(art.data)
					console.log(that.list);
					uni.stopPullDownRefresh();
					that.page++;
					if(art.data.length == 0 || page == art.meta.last_page){
						that.status = 'noMore';
						return
					}
					that.status = 'more';
				} else {
					that.showToast(res.data.errMsg)
				}
			}).catch(err => {
				that.showToast(err.text)
				console.log(err)
			})
		},
		videoLike(item,index){
			console.log(item);
			let data = {
				feed_id: item.id,
				user_id: getApp().globalData.userInfo.user_id
			}
			// return
			videoLike(data).then(res => {
				console.log('videoLike',res)
				if(res.data.code == 201){
					that.$set(that.list[index],'is_like',true)
					let count = that.list[index].like_count
					count++
					that.$set(that.list[index],'like_count',count)
					// that.feedData[idx].is_like = true;
					// that.feedData[idx].like_count++;
					// that.showToast(res.data.data)
					uni.showToast({
					    title: res.data.data,
					    duration: 1500,
					    icon: 'success'
					});
				} else if (res.data.code == 400) {
					uni.navigateTo({
						url: '../login/login'
					});
				} else {
					that.showToast(res.data.message)
				}
			}).catch(err => {
				that.showToast(err.text)
				console.log(err)
			})
		},
		toDetail(id){
			uni.navigateTo({
				url: './orderDetail?id=' + id
			})
		},
		newGuid() {
			let s4 = function() {
				return ((65536 * (1 + Math.random())) | 0).toString(16).substring(1);
			};
			return (s4() + s4() + '-' + s4() + '-4' + s4().substr(0, 3) + '-' + s4() + '-' + s4() + s4() + s4()).toUpperCase();
		},
		showToast(e) {
			uni.showToast({
				title: e,
				icon: 'none',
				mask: true,
				duration: 2000
			})
		}, 
	}
}
</script>

<style lang="scss">
.video{
	.cu-card{
		.cu-item{
			margin-top: 20rpx;
			&:first-child{
				margin: 0;
			}
			.cu-list{
				.cu-item{
					padding: 0;
					&::after{
						display: none;
					}
					
				}
			}
			.text-content{
				font-size: 32rpx;
			}
		}
	}
	.emptyContainer{
		position: fixed;
		top: 40%;
		left: 50;
		width: 100%;
		text-align: center;
		
	}
}
</style>
